.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#editorCase {
  float: left;
  width: 35%;
  height: 62px;
 margin: 0 20px;
 background-color: red;
 border: 1px solid black;
}

#editorCase h3{
  text-align: left;
  padding-left: 20px;

}

#editor {
  height: 300px;
  position: absolute;
  left: 20px;
  width: 34.5%;
}

#preview {
  float:right;
  width: 55%;
border: 2px solid black;
margin-right: 20px;
text-align: left;
padding: 0 20px;
}

